export default {
  name: "Index",
  data () {
    return {
      list: wordList,
      oldListNum: JSON.parse(
        localStorage.array || JSON.stringify({ oldListNum: [], newListNum: [] })
      ).oldListNum,
      newListNum: JSON.parse(
        localStorage.array || JSON.stringify({ oldListNum: [], newListNum: [] })
      ).newListNum,
      showId:
        JSON.parse(
          localStorage.details ||
            JSON.stringify({ old: 0, new: 0, time: 0, showId: 0 })
        )?.showId || 0,
      currentEnglish:'bride',
      currentWordSex:'n.',
      currentChinese:'',
      showChinese: false,
      oldDate: 0,
      timer: '',
      details: JSON.parse(
        localStorage.details ||
          JSON.stringify({ old: 0, new: 0, time: 0, showId: 0 })
      ),
      first:true
    }
  },
  props: [],
  components: {},
  watch: {
    showId () {
      const myDate = new Date()
      this.details.time = myDate.getMinutes() - this.oldDate
      localStorage.details = JSON.stringify(this.details)
      localStorage.array = JSON.stringify({
        oldListNum: this.oldListNum,
        newListNum: this.newListNum
      })
    }
  },
  computed: {},
  created () {
    const myDate = new Date()
    this.oldDate = myDate.getMinutes()
    this.timer = setTimeout(() => {
      this.showChinese = true
    }, 5000)
  },
  methods: {
    preWord () {
      this.first=true
      clearTimeout(this.timer)
      this.showChinese = false
      this.timer = setTimeout(() => {
        this.showChinese = true
      }, 5000)
      if (this.newListNum.indexOf(this.showId) === -1) {
        this.details.new++
        this.newListNum.push(this.showId)
      }
      this.showId > 0 ? this.showId-- : (this.showId = 0)
      this.details.showId = this.showId
      this.currentEnglish = this.list[this.showId].english
      this.currentWordSex = this.list[this.showId].wordSex
      this.currentChinese = this.list[this.showId].chinese
    },
    next(){
      this.first=false
      clearTimeout(this.timer)
      this.showChinese = true
    },
    nextWord () {
      this.first=true
      clearTimeout(this.timer)
      this.showChinese = false
      this.timer = setTimeout(() => {
        this.showChinese = true
      }, 5000)
      if (this.newListNum.indexOf(this.showId) === -1) {
        this.details.new++
        this.newListNum.push(this.showId)
      }
      this.showId < this.list.length - 1
        ? this.showId++
        : (this.showId = this.list.length - 1)
      this.details.showId = this.showId
      this.currentEnglish = this.list[this.showId].english
      this.currentWordSex = this.list[this.showId].wordSex
      this.currentChinese = this.list[this.showId].chinese

    },
    handleLoading () {
      this.showChinese = true
    }
  }
  
}
</script>

<style scoped>
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes downAndScale {
  0% {
    transform: translateY(-4rem) scale(0.8);
  }
  80% {
    transform: translateY(0) scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}
* {
  margin: 0;
  padding: 0;
}

html {
  font-size: 100px;
  color: #333;
}

.icons img {
  width: 0.2rem;
  height: 0.2rem;
  margin: 0.2rem;
  position: absolute;
}

img.icon1 {
  left: 0;
  top: 0;
}

img.icon2 {
  right: 0;
  top: 0;
}

.imformation {
  margin: 0.6rem auto;
  display: flex;
  justify-content: space-between;
  font-size: 0.16rem;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
